        <Img
          fluid={data.eligibility.childImageSharp.fluid}
          className="eligibility-image"
        />
        <ul>
          <li>
            To enter the program, you should pass the test based on
            prerequisites given. The pass percentage is 75%.
          </li>
          <li>
