export default eligibility

export const query = graphql`
  query {
    eligibility: file(relativePath: { eq: "eligibility.png" }) {
      childImageSharp {
        fluid(maxWidth: 1500) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`
